@import "~antd/dist/antd.css";
//$main_color: #19acc3;
$main_color: #F3F6FF;
$text_color1: #42427D;
$text_color2: #A0AAC8;

* {
  margin: 0;
  padding: 0;
  color: #000;
}
/*body, #root, .main {*/
/*  height: 100%;*/
/*}*/
#root {
  padding: 10px;
  box-sizing: border-box;
}
.main {
  background-color: $main_color;
  border-radius: 70px;
  padding: 18px 18px 18px 64px;
  box-sizing: border-box;
  display: flex;
  height: calc(100vh - 24px);
}

.navigation {
  width: 224px;
  padding-top: 60px;
  &__icon {
    margin-right: 30px;
  }
  & ul {
    list-style-type: none;
  }
  &__title_wrapper {
    display: flex;
    padding: 0 40px;
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
    height: 60px;
    align-items: center;
    margin-left: -18px;
    background-color: inherit;
    color: $text_color2;
  }
  &__icon {
    color: $text_color2;
  }

  &__active_link {
    background: #fff;
    & .navigation__icon {
      color: $text_color1;
    }
    & .navigation__title_wrapper {
      color: $text_color1;
    }
  }
  &__base_link {
   display: flex;
    flex-direction: column;
    text-decoration: none;
    cursor: pointer;
  }
}
.top_radial_devider, .bottom_radial_devider {
  height: 15px;
  background-color: $main_color;
}
.top_radial_devider {
  border-bottom-right-radius: 20px;
}
.bottom_radial_devider {
  border-top-right-radius: 20px;
}
.nav_header {
  margin-bottom: 100px;
  &__title {
    font-size: 30px;
    color: $text_color1;
    font-family: sans-serif;
  }
}

.content {
  flex: 1;
  background-color: #fff;
  border-radius: 54px;
  padding: 40px 40px 40px 40px;
}

.users_list {
  &__columns_wrapper {
    padding: 8px;
  }
  &__colummns_input {
    width: 188px;
    marginBottom: 8px;
    display: block;
  }
  &__send_btn {
    margin-top: 25px;
  }
  &__send_btn_primary {
    margin-top: 25px;
    span {
      color: #fff
    }
  }
  &__btn_wrapper {
    margin-top: 40px;
    justify-content: space-evenly;
    align-items: center;
    width: 100%
  }
}

.send_push {
  &__header {
    height: 40px;
    align-items: center;
    display: flex;
    margin-bottom: 25px;
  }
  &__row {
    margin-bottom: 25px;
  }
}

.settings {
  & h3 {
    margin-top: 25px;
  }
  & input {
    margin-right: 25px;
  }
  &__row {
    display: flex;
  }
  &__title {
    width: 30%;
    & button {
      display: flex;
      align-items: center;
    }
    input {
      outline: none;
      box-shadow: none;
      &:focus {
        //border-color: unset;
      }
    }
  }

  &__title_spin {
    height: 20px;
    margin-right: 15px;
  }
}
[data-theme='compact'] .ant-advanced-search-form,
.ant-advanced-search-form {
  padding: 40px;
  background: $main_color;
  //border: 1px solid #d9d9d9;
  border-radius: 70px;
}

[data-theme='compact'] .ant-advanced-search-form .ant-form-item,
.ant-advanced-search-form .ant-form-item {
  display: flex;
}

[data-theme='compact'] .ant-advanced-search-form .ant-form-item-control-wrapper,
.ant-advanced-search-form .ant-form-item-control-wrapper {
  flex: 1;
}
